<template>
  <div @click="fire" :class="필터" class="filter-item" :style="`background-image:url(${이미지})`">
    <slot></slot>
  </div> 
</template>

<script>
export default {
  name : 'FilterBox',
  props : {
    이미지 : String,
    필터 : String,
  },
  methods : {
    fire(){
      this.emitter.emit('filterInfo',this.필터);
      // console.log(this.필터);
    }
  },
}
</script>

<style>
.filter-item {
  width: 100px;
  height: 100px;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color : white;
  background-size: cover;
  background-position : center;
}
</style>