<template>
  <div class="header">
    <ul class="header-button-left">
      <li @click="step--">Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if=" step == 1 " @click="step++">Next</li>
      <li v-if=" step == 2 " @click="publish">올리기</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>

  <Container :필터명="필터명" @text="작성글 = $event" :postdata="postdata" :step="step" :이미지="이미지" />

  <div class="more" v-if="step == 0">
    <button class="moreBtn" @click="more">더보기</button>
  </div>

  <div class="footer">
    <ul class="footer-button-plus" v-if="step == 0 || step == 1">
      <input @change="upload" accept="image/*" type="file" id="file" class="inputfile" />
      <label for="file" class="input-plus">+</label>
    </ul>
 </div>

</template>

<script>
import Container from './components/ContainerFirst.vue';
import postdata from './assets/Data.js';
import axios from 'axios';

export default {
  name: 'App',
  data(){
    return{
      postdata : postdata,
      count : 0,
      step : 0,
      이미지 : '',
      작성글 : '',
      필터명 : '',
      // 프로필 : "style"
    }
  },
  components: {
    Container,
  },
  methods : {
    more(){
      axios.get(`https://codingapple1.github.io/vue/more${this.count}.json`)
      .then((결과)=>{
        // console.log(결과.data);
        this.postdata.push(결과.data);
        this.count++;
      })
    },
    upload(e){
      let 파일 = e.target.files;
      // console.log(파일[0]);
      let url = URL.createObjectURL(파일[0]);
      // console.log(url);
      this.이미지 = url;
      this.step = 1;
    },
    publish(){
      var 게시물 = {
        name: "Kang Dayeon",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.이미지,
        likes: 100,
        date: "May 1",
        liked: false,
        content: this.작성글,
        filter: this.필터명,
      };
      this.postdata.unshift(게시물)
      this.step = 0;
    },
  },
  mounted(){
    this.emitter.on('filterInfo', (a)=>{
      this.필터명 = a;
    })
  }
}
</script>

